@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap);
.Recipe_recipe__3WC6V{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    margin: 10px 10px;

}

.Recipe_image__3mYXL{
    border-radius: 50%;
    width: 100px;
    height: 100px;
}
*{
    font-family: 'Baloo Tammudu 2', cursive;
}
.App {
    min-height: 100vh;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.search-form {
    min-height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar{
    width: 50%;
    padding: 10px;
    border: none;
}

.search-button {
    background: lightcoral;
    border: none;
    padding: 10px 20px;
    color: white;
}

.recipe{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 320px;  
}

@media only screen and (max-width: 770px){
    .img {
        width: 70%;
    }
}
